.Alert {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
}

.Alert .InsideAlert {
  width: 80%;
  height: 55%;
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #909090;
  -webkit-box-shadow: 0 0 1rem #fff inset, 0 0 1rem #fff;
          box-shadow: 0 0 1rem #fff inset, 0 0 1rem #fff;
  border-top-right-radius: 24px;
  border-bottom-left-radius: 24px;
}

.Alert .InsideAlert .Hide {
  display: none;
}

.Alert .InsideAlert .closeAlert {
  margin: 4px;
  padding: 0.1rem;
  -webkit-box-shadow: 0 0 1rem #fff, 0 0 1rem #fff inset;
          box-shadow: 0 0 1rem #fff, 0 0 1rem #fff inset;
  margin-left: auto;
  border-radius: 100%;
  color: #909;
  cursor: pointer;
}

.Alert .InsideAlert .warning {
  margin: auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 0 0.5rem;
}

.Alert .InsideAlert .warning #alertIcon {
  margin: auto;
}

.Alert .InsideAlert .warning .warningText {
  text-align: center;
  font-size: 1.7rem;
  font-weight: bold;
  color: #909;
}

.Alert .InsideAlert .warning .warningConfirmBtns {
  margin: 1rem 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
}

.Alert .InsideAlert .warning .warningConfirmBtns button {
  padding: 1rem;
  font-size: 1.4rem;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
}

.Alert .InsideAlert .warning .warningConfirmBtns .Yes {
  background: red;
  color: lightblue;
  -webkit-box-shadow: 0 0 0.5rem lightblue;
          box-shadow: 0 0 0.5rem lightblue;
}

.Alert .InsideAlert .warning .warningConfirmBtns .No {
  background: lightblue;
  color: red;
  -webkit-box-shadow: 0 0 0.5rem lightblue;
          box-shadow: 0 0 0.5rem lightblue;
}

.Alert .InsideAlert .workDone {
  margin: auto;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.Alert .InsideAlert .workDone .workDoneLabel {
  font-size: 2rem;
  color: greenyellow;
  font-weight: bold;
}
